import { Base64 } from 'js-base64';

export class StringUtils {
    static EMPTY = "";

    /**
     * Check whether a string is null or empty.
     * @param str value
     * @returns true : if string is null or empty
     */
    static isNullOrEmpty(str) {
        if (!str || str === null || str === StringUtils.EMPTY) {
            return true;
        }
        return false;
    }

    /**
     * Encoding str to Base64
     * @param str value
     * @returns Base64 str
     */
    static base64Encode(str) {
        if (!str || str === null) {
            return null;
        } else {
            return Base64.encode(str);
        }
    }

    /**
     * Decode Base64 to str
     * @param Base64 str
     * @returns Value decode
     */
    static base64decode(str) {
        if (!str || str === null) {
            return null;
        } else {
            return Base64.decode(str);
        }
    }



    /**
     * Removes " ", "ã€€", and \t from the beginning of the 'str'. Returns 'defaultStr' if 'str' is null
     * or trimmed string is "".
     *
     * @param str
     * @param defaultStr
     * @return trimmed string
     */
    static trimL(str, defaultStr) {

        let ret = str;

        if (!ret || ret === null) {
            ret = "";
        }
        // remove ' ', 'ã€€', '\t'
        while (ret.startsWith(" ") || ret.startsWith("ã€€") || ret.startsWith("\t") || ret.startsWith("\n")) {
            ret = ret.substring(1);
        }

        if (ret === "") {
            ret = defaultStr;
        }
        return ret;
    }

    /**
     * Removes " ", "ã€€", and \t from the end of the 'str'. Returns 'defaultStr' if 'str' is null or
     * trimmed string is "".
     *
     * @param str
     * @param defaultStr
     * @return trimmed string
     */
    static trimR(str, defaultStr) {

        let ret = str;

        if (!ret || ret === null) {
            ret = "";
        }
        // Delete " ", "ã€€", and \t
        while (ret.endsWith(" ") || ret.endsWith("ã€€") || ret.endsWith("\t")) {
            ret = ret.substring(0, ret.length() - 1);
        }

        if (ret === "") {
            ret = defaultStr;
        }
        return ret;
    }

    /**
     * Removes " ", "ã€€", and \t from both ends of the 'str'. Returns 'defaultStr' if 'str' is null or
     * trimmed string is "".
     *
     * @param str
     * @param defaultStr
     * @return trimmed string
     */
    static trim(str, defaultStr) {

        let ret = str;
        // Delete " ", "ã€€", and \t
        ret = StringUtils.trimL(ret, defaultStr);
        //ret = StringUtils.trimR(ret, defaultStr);

        return ret;
    }

    static nonAccentVietnamese(str) {
        str = str.toLowerCase();
        str = str.replace(/Ã |Ã¡|áº¡|áº£|Ã£|Ã¢|áº§|áº¥|áº­|áº©|áº«|Äƒ|áº±|áº¯|áº·|áº³|áºµ/g, "a");
        str = str.replace(/Ã¨|Ã©|áº¹|áº»|áº½|Ãª|á»|áº¿|á»‡|á»ƒ|á»…/g, "e");
        str = str.replace(/Ã¬|Ã­|á»‹|á»‰|Ä©/g, "i");
        str = str.replace(/Ã²|Ã³|á»|á»|Ãµ|Ã´|á»“|á»‘|á»™|á»•|á»—|Æ¡|á»|á»›|á»£|á»Ÿ|á»¡/g, "o");
        str = str.replace(/Ã¹|Ãº|á»¥|á»§|Å©|Æ°|á»«|á»©|á»±|á»­|á»¯/g, "u");
        str = str.replace(/á»³|Ã½|á»µ|á»·|á»¹/g, "y");
        str = str.replace(/Ä‘/g, "d");
        // Some system encode vietnamese combining accent as individual utf-8 characters
        str = str.replace(/\u0300|\u0301|\u0303|\u0309|\u0323/g, ""); // Huyá»n sáº¯c há»i ngÃ£ náº·ng
        str = str.replace(/\u02C6|\u0306|\u031B/g, ""); // Ã‚, ÃŠ, Ä‚, Æ , Æ¯
        str = str.replace(/\s/g, "-");
        return str;
    }

    static truncateString(str, num) {
        if (str.length > num) {
            return str.slice(0, num) + "...";
        } else {
            return str;
        }
    }
}